
import { defineComponent, PropType } from 'vue';

const Component = defineComponent({
  name: 'Vbutton',
  emits: ['onClick'],
  props: {
    buttonType: {
      type: String as PropType<'button' | 'submit' | 'reset'>,
      default: 'button',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    buttonClass: {
      type: String,
      default: 'btn-primary',
    },
  },
});

export default Component;
